const lostMicrophone = () => {

const btnLostMicrophone =  document.querySelector('.fab-buttons__link');


btnLostMicrophone.addEventListener('click', (e)=> {
  e.preventDefault()
  const list = document.querySelectorAll('.start-stop');

  if(!list[0].classList.contains('btnDeactiv') && !list[2].classList.contains('btnDeactiv')){
   return console.log('кнопка не нажата');
  } else {
    console.log('какая-то кнопка нажата');
  }
for (const iterator of list) {
  const li = iterator.className.split(' ');
  if(li.length[4] == undefined && !li.includes("btnDeactiv")){
    setTimeout(() => {
      iterator.scrollIntoView({block: "center", behavior: "smooth"})
    }, 200);
    }
}
})
// btnDeactiv

}
export default lostMicrophone