import app  from "./app";
import chech  from "../services/chech";
import matchSearch  from "../services/matchSearch";
import db from "../db/db.json"
// const db = require("../assets/db.json")
const standardQuestions = (selector, selectorCit = 'standart')=>{

  for (const key in selector) {
    const element = selector[key];
    if (Object.hasOwnProperty.call(selector, key)) {
      const div = document.createElement('div')
      div.classList.add('tinRightIn')
      div.innerHTML = `
      <div class="wrapper ${selectorCit}question${key} ${'quetion'+key === 'quetion1'? 'q':'hide' } ">
      <form action="#">
          <div class="row">
            <div class="quesion">
              <div class="quesionNum">${key}</div>
              <p id="textQuesion">${element[0]}</p> 
            </div>
            <div class="wrap-button">
              <!-- <p id="textQuesion">Enter Text</p>  -->
                
               <audio controls class="audio"  style="width:100%;max-width:260px;" id="aud">
                <source src="${element[2]? element[2]:'assets/voise/samCh.mp3'}" type="audio/mp3">
                <source src="${element[2]? element[2]:'assets/voise/samCh.mp3'}">
                <p>Your browser doesn't support HTML5 audio. Here is a 
                <a href="viper.mp3">link to the audio</a> instead.</p>
              </audio>
              <button class="btn-remove btn btn-remove${key}">
              <img class="icon" src="assets/img/cors.png" alt="remove">
              </button>
              <button class="btn-remove btn start-stop start-stop${key}">
              <img class="icon" id="voisIconi${key}" src="assets/img/micro.png" alt="vois">
              
              </button>
              <button class="btn-remove btn pause pause${key}">
              <img class="icon" src="assets/img/mute.png" alt="stop">
              
              </button>
            </div>
            <textarea class="lasss lasss${key}" value=""></textarea>
        
          </div>
        
        </form> 
        <!-- .wrapper -->
        <div class="respons">
        <button id="btnResp${key}" class="accordion">Ответ</button>
         <div class="panel">
            <p class="textOler">${element[1]? matchSearch(element[1]):'ответа пока нет'}</p>
        </div>
        </div>
  
      </div>
      
      `
         //  <p  style="text-align: center;" >Ответ</p>
        //  document.querySelector('.quetion1').classList.remove('hide')
  
      document.querySelector('.wrapperPagestart').append(div)
      setTimeout(() => {
        
        app('.start-stop' + key,
            '.pause'+key,
            '.btn-remove'+key,
            '.lasss' + key,
            element[2],
            selectorCit+'question'+key,
            '#btnResp'+ key,
            '#voisIconi'+key) 
            
            // проверяется для внесения от инпутов из локалки
        if (localStorage.getItem(selectorCit+'question'+key) !== null) {
          // …то отображаем его содержимое в нашем редакторе
          let local = localStorage.getItem(selectorCit+'question'+key),
          cor = `${local}`
          localStorage.setItem(selectorCit+'question'+key, chech(local));
          document.querySelector('.lasss'+key).value = cor ;
        } 
        // quetionAdd(`quetion${key}`)
      }, 1000);
      
    }
  
  }
  


}

export default standardQuestions