const voiceQuestion  = (iterator,vioseSkib) => {
  const icon_material_li = document.querySelector('.icon-material_li');
  if (vioseSkib) {
    return console.log('somalet');
  }

  if(!icon_material_li.classList.contains('icon-material_li_mute')){
document.querySelectorAll("audio").forEach(audio=> audio.pause())
    iterator.volume = 1.0;
    iterator.play()
  }

}
export default voiceQuestion