
const openAllQuestion = () => {
const icon_material_Open_all = document.querySelector('.icon-material_gp')

icon_material_Open_all.addEventListener('click', (e)=>{
  e.preventDefault(); 

  // quetionAdd(true, true)
  const body = document.querySelector('body');

  const listAll = document.querySelectorAll('.wrapper')

  const titleQuestionvois = document.querySelector('.wrapperTutle')

  for (const iterator of listAll) {
    if(iterator.classList.contains('hide') && iterator.classList.contains('quetion1') !== 'quetion1'){
     titleQuestionvois.classList.add('relev')


      iterator.classList.remove('hide')
      
      
      setTimeout(() => {
        iterator.scrollIntoView({block: "center", behavior: "smooth"})
      }, 1200);
      body.style.height = body.offsetHeight +350+"px"
     

    }
  }


})
}
export default openAllQuestion
