const togallVois = () => {

  const doc = document.querySelectorAll('.fab-buttons')

  doc[0].children[2].addEventListener('click', (e)=> {
    e.preventDefault()
    toggaleButton('.icon-material_li','icon-material_li_mute')

    const icon_material_li = document.querySelector('.icon-material_tw');

    if(icon_material_li.classList.contains('icon-material_tw_NonStop')){
      icon_material_li.classList.remove('icon-material_tw_NonStop')
    }
  })
  doc[0].children[1].addEventListener('click', (e)=> {
    e.preventDefault()
    toggaleButton('.icon-material_tw','icon-material_tw_NonStop')

  })

  function toggaleButton(elemSelector,toggalClass) {
    const icon_material_li = document.querySelector(elemSelector);
    icon_material_li.classList.toggle(toggalClass)
  }
}
export default togallVois