import db from'../db/db.json'

const whereStay = () => {
  const doc = document.querySelectorAll('.fab-buttons')
  doc[0].children[4].addEventListener('click', (e)=> {
e.preventDefault()

 const sel =  localStorage.getItem('WhereStayUser')
    if (sel) {
    let selectorQuestionLevel =  sel.replace(/(question)(\d)+/,'');
    let numberQuestion=  sel.replace(/(tick|cross|heart|flower)(question)/,'');
      

    // if (selectorQuestionLevel === 'tick') {
    //   selectorListQ = db['styles'][0]
    //   // sel = 'tick'
    // } 
    // if (selectorQuestionLevel === 'cross') {
    //   selectorListQ = db['styles'][1]
    //   // sel = 'cross'
    // }
    // if (selectorQuestionLevel === 'heart') {
    //   selectorListQ = db['styles'][2]
    //   // sel = 'heart'
    // }
    // if (selectorQuestionLevel === 'flower') {
    //   selectorListQ = db['styles'][3]
    //   // sel = 'flower'
    // }

    let listAll = document.querySelectorAll('.wrapper')
    const titleQuestionvois = document.querySelector('.wrapperTutle')
    if(listAll){

     document.querySelector(`.${selectorQuestionLevel}`).click();

    }
    setTimeout(() => {
      listAll = document.querySelectorAll('.wrapper')
      for (let index = 0; index <= numberQuestion; index++) {
        const iterator = listAll[index];
  
        if(iterator && iterator.classList.contains('hide') && iterator.classList.contains('quetion1') !== 'quetion1'){
          titleQuestionvois.classList.add('relev')
     
     
           iterator.classList.remove('hide')
           
           
           setTimeout(() => {
             iterator.scrollIntoView({block: "center", behavior: "smooth"})
           }, 1200);
          //  body.style.height = body.offsetHeight +350+"px"
          
     
         }
      }
      
    }, 1500);

    } else {
      console.log('new User');
    }
  localStorage.setItem('QuestAWhereStay',false)
  
})

}

export default whereStay