import voiceQuestion from "./voiceQuestion";

const quetionAdd = (vioseSkib) => {
  const body = document.querySelector('body');
  let NonStop = document.querySelector('.icon-material_tw')

if (NonStop.classList.contains('icon-material_tw_NonStop')) {
        const listAll = document.querySelectorAll('.wrapper')
        const li = document.querySelector('.wrapperTutle')

    for (let i = 0; i < listAll.length; i++) {
          const iterator = listAll[i];
          
            let sel = iterator.children[0][2];
            sel.click();

          
      if(iterator.classList.contains('hide') && iterator.classList.contains('quetion1') !== 'quetion1'){
            let elementBefor = listAll[i - 1];
            let elementAfter = listAll[i + 1];
            
       
            setTimeout(() => {
              if(!vioseSkib){
                voiceQuestion(iterator.children[0].firstElementChild.children[1].children[0],false)
        
              }
              
            }, 1000);

       
                // тут начинается включение полс перерыва
                iterator.children[0].firstElementChild.children[1].children[0].onended = function() {
             
                  listAll.forEach(item=> item.children[0][2].click())
                  setTimeout(() => {
                    
                    if(elementAfter.classList.contains('hide')){
                      iterator.children[0][1].click();
                    }  
                  
                  }, 800);
              };
              //end paused
        

       

           
            li.classList.add('relev')
          
            iterator.classList.remove('hide')
            setTimeout(() => {
              iterator.scrollIntoView({block: "center", behavior: "smooth"})
            }, 1200);
            body.style.height = body.offsetHeight +350+"px"
           
            break;
            //end if iteration element 
          }
          //end for 1  
        }

} else {
  
  const listAll = document.querySelectorAll('.wrapper')
  const li = document.querySelector('.wrapperTutle')

  for (const iterator of listAll) {
    if(iterator.classList.contains('hide') && iterator.classList.contains('quetion1') !== 'quetion1'){
      li.classList.add('relev')
      if(!vioseSkib){
        voiceQuestion(iterator.children[0].firstElementChild.children[1].children[0],false)

      }
      iterator.classList.remove('hide')
      setTimeout(() => {
        iterator.scrollIntoView({block: "center", behavior: "smooth"})
      }, 1200);
      body.style.height = body.offsetHeight +350+"px"
     
      break;
    }
    //end forof
  }
// end else NonStop
}
}
export default quetionAdd