import multiPlepresButtons  from "../services/multiPlepresButtons";
import chech  from "../services/chech";
import automationSizeInput from "../services/automationSizeInput";
import voiceQuestion from "../services/voiceQuestion";
let count = 1
const  app = (selStart, selStop, selRemove, selTextare, selPahtVoid, selLocal, selBtnResp,selVoisIcPuls) => {
  const recognizer = new (window.SpeechRecognition || window.webkitSpeechRecognition || window.mozSpeechRecognition || window.msSpeechRecognition)();
  // var SpeechRecognition = SpeechRecognition || webkitSpeechRecognition;
  
  // const recognizer =  new SpeechRecognition(); 
// Ставим опцию, чтобы распознавание началось ещё до того, как пользователь закончит говорить
recognizer.interimResults = true;

// Какой язык будем распознавать?
recognizer.lang = 'ru-Ru';
global.recog = recognizer;

const btnStartVoise = document.querySelector(selStart)
const letstop = document.querySelector(selStop)
const remove = document.querySelector(selRemove)
const textare = document.querySelector(selTextare)
const btnResp = document.querySelector(selBtnResp)
const material_fb = document.querySelector('.icon-material_fb')
let resal = true
let setinte 

btnResp.addEventListener('click',  function(e) {
  e.preventDefault();
  this.classList.toggle("active");
  let panel = this.nextElementSibling;
  if (panel.style.maxHeight) {
    panel.style.maxHeight = null;
  } else {
    panel.style.maxHeight = panel.scrollHeight + "px";
  }
})

remove.addEventListener('click', (e)=>{
  e.preventDefault()


  let target = null 
  if(e.target.localName === "button") {
    target = e.target.parentNode.nextElementSibling
  } else {
    target = e.target.parentNode.parentNode.nextElementSibling
  }
  target.value = ''
  target.innerHTML = ''
  localStorage.setItem(selLocal, '');
})

recognizer.onend = () => {
  console.log("Распознавание голоса закончено" + selStart);
  multiPlepresButtons(false,selStart)

  if (!resal) return;
  recognizer.start();
};

recognizer.onstart = (e) => {

    const el = textare;
  if(el.scrollHeight > 100){
    el.style.cssText = 'height:' + el.scrollHeight + 'px';
  } 
  if (!textare.value) {
    el.style.cssText = 'height:100px;';
  }

  console.log("Распознавание голоса запущено" + selStart);

  multiPlepresButtons(true,selStart)
};

recognizer.onerror = ({ error }) => {
  console.error(error);
};

// const audio = new Audio();
// audio.preload = 'auto';
// audio.src = `./${selPahtVoid}`;

const icon_material_li = document.querySelector('.icon-material_li');

  if (selStart == '.start-stop1' && !icon_material_li.classList.contains('icon-material_li_mute')) {
    voiceQuestion(document.querySelector('.start-stop1').offsetParent.children[0],false)
    // audio.addEventListener('play', function(e){
    }
   
    //Если полььзователь включает звук у аудио то у дургих он отключается
    document.querySelectorAll("#aud").forEach(item=> {

      item.onplay = (event) => { 
       
        document.querySelectorAll("audio").forEach(audio=> {
          if(audio == event.target){
            
          } else {
            audio.pause()
          }
        })
      };

    })

btnStartVoise.addEventListener('click', (event)=> { 
  event.preventDefault()
  //set localStorage Where Stay user 
  localStorage.setItem('WhereStayUser',selLocal)

  material_fb.classList.add('icon-material_Pulsev')
  
  resal = true
  let target = null

  if(event.srcElement.nodeName === "IMG") {
    target = event.srcElement.offsetParent.parentElement.nextElementSibling
  } else {  
    target = event.target.parentElement.nextElementSibling
  }
      document.querySelector(selVoisIcPuls).classList.add('iconActive')
          
        recognizer.start();
        recognizer.onresult = function (event) {
                let result = event.results[event.resultIndex];
                if (result.isFinal) {
                  console.log('Вы сказали в тоге: ' + result[0].transcript);
                 
                  if (localStorage.getItem(selLocal) !== null) {
                    // …то отображаем его содержимое в нашем редакторе
                    let local = localStorage.getItem(selLocal),
                    cor = `${local} ${result[0].transcript}`
                    localStorage.setItem(selLocal, chech(cor));
                    target.value = [chech(cor.trim())] ;
                  } else {
                    let local = localStorage.getItem(selLocal),
                    cor = `${local} ${result[0].transcript}`
                    target.innerText =  chech(cor.trim())
                    localStorage.setItem(selLocal, chech(cor.trim()));
                  }     
                } else {
                
                  console.log('Промежуточный результат: ', result[0].transcript);
                  if (localStorage.getItem(selLocal) !== null) {
                    // …то отображаем его содержимое в нашем редакторе
                    
                    let local = localStorage.getItem(selLocal),
                    cor = `${local} ${result[0].transcript}`;
                    target.value = chech(cor.trim());
                  } else {
                    
                    let local = localStorage.getItem(selLocal),
                    cor = `${local} ${result[0].transcript}`
                    target.value =  chech(cor.trim())
                   
                  }
      
                }
              };
})

letstop.addEventListener('click', (event)=> {
  event.preventDefault()
  clearTimeout(setinte);
  material_fb.classList.remove('icon-material_Pulsev')
  if(event.target.localName ==="button" ){
    event.target.parentNode.children[2].children[0].classList.remove('iconActive');
    
  }else {
    if(event.srcElement.offsetParent.form[1].children[0].attributes[0].ownerElement.className.slice(5) === 'iconActive'){
     event.srcElement.offsetParent.form[1].children[0].attributes[0].ownerElement.classList.remove('iconActive') 
    }
  }

  btnStartVoise.classList.add('start')
  resal = false
  recognizer.abort();
})

 
textare.addEventListener('input', function (event) {
  event.preventDefault()
    localStorage.setItem(selLocal,chech(textare.value));
});



function stop(params) {
  resal = false
  // recognizer.abort();
  params.abort();
}


automationSizeInput(textare)

}

export default app